.dez-img-overlay12:after {
  content: "";
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#192665+0,09123f+49,222845+100 */
  background: rgb(25, 38, 101);
  /* Old browsers */
