  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#192665', endColorstr='#222845', GradientType=1);
  /* IE6-9 fallback on horizontal gradient */
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 0;
  opacity: 0.8;
  z-index: 1;
  -webkit-transition: all 0.3s linear;
