/* Job Categories */
.job-categories .icon-bx-wraper {
	padding: 40px 10px;
	margin-bottom: 20px;
	/* background: #2e55fa; */
	background: #d2293a;
	border-radius: 10px;
	text-align: center;
	-moz-transition: all 0.5s;
	-webkit-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	overflow: hidden;
	box-shadow: 0 0 10px 0 rgba(0, 24, 128, 0.1);
}

.job-categories .icon-bx-wraper .dez-tilte {
	display: block;
	width: 100%;
	color: #000;
	font-size: 18px;
}

.job-categories .icon-bx-wraper .icon-content {
	position: relative;
	z-index: 1;
}

.job-categories .icon-bx-wraper .rotate-icon {
	position: absolute;
	font-size: 90px;
	color: #000;
	bottom: 10px;
	line-height: 90px;
	right: 20px;
	opacity: 0.05;
	text-align: right;
}

.job-categories .icon-bx-wraper:after {
	background-image: url(../images/categories-bg.png);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: bottom;
	content: "";
	width: 100%;
	height: 110px;
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	-moz-transition: all 0.5s;
	-webkit-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.job-categories .icon-bx-wraper:hover:after {
	height: 0;
}

.job-categories .icon-bx-wraper:hover {
	/* background-color:#2e55fa; */
	background-color: #d2293a;
	transform: scale(1.05);
	-moz-transform: scale(1.05);
	-webkit-transform: scale(1.05);
	-ms-transform: scale(1.05);
	-o-transform: scale(1.05);
	box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
}

.job-categories .icon-bx-wraper .rotate-icon,
.job-categories .icon-bx-wraper .dez-tilte,
.job-categories .icon-bx-wraper {
	color: #fff;
}

.job-categories .icon-bx-wraper:hover .icon-md i {
	color: #fff;
}

