/* LIKE BTN ===================== */
/* The like-btn */
.like-btn {
	display: block;
	position: absolute;
	padding-left: 35px;
	margin-bottom: 12px;
	cursor: pointer;
	font-size: 22px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	font-family: FontAwesome;
	top: 25px;
	right: 25px;
}

.like-btn input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}

.checkmark {
	position: absolute;
	top: 0;
	left: 0;
	height: 30px;
	width: 30px;
	border: 1px solid #1c2957;
	border-radius: 20px;
}

.like-btn input:checked~.checkmark {
	background-color: #2196f3;
}

.checkmark:after {
	content: "\f08a";
	position: absolute;
	display: none;
}

.like-btn input:checked~.checkmark:before {
	width: 100%;
	height: 100%;
	background-color: #1c2957;
	left: 0;
	top: 0;
	border-radius: 20px;
	color: #fff;
	padding: 3px 6px;
}

.like-btn .checkmark:after {
	left: 9px;
	top: 5px;
	width: 5px;
	height: 10px;
}

.like-btn .checkmark:before {
	content: "\f08a";
	left: 6px;
	top: 3px;
	position: absolute;
	font-size: 16px;
	font-weight: normal;
	color: #1c2957;
}

